import React from 'react'
import {Container, Form, Button } from 'react-bootstrap';

export default function BottomForm() {
 return (
  <div className='position'>
  <Container>
   <Form>
    <Form.Group controlId="formBasicEmail">
      <Form.Control type="email" placeholder="Name" className='input height form_elements'/>
    </Form.Group>

    <Form.Group controlId="formBasicPassword">
      <Form.Control type="password" placeholder="Email" className='input height form_elements'/>
    </Form.Group>
   <Button variant="primary" type="submit" className='start form_elements _width'>
     Submit
   </Button>
  </Form>
 </Container>
  </div>
 )
}

